import styled from 'styled-components'

import NoticeImage from "../assets/notice.jpg"
import { Button } from './Button'

const OuterContainer = styled.div`
	width: 100%; 
	height: 100%;
	z-index: 99;
	background-color: rgba(0,0,0,0.5);
	position: fixed;
`

const InnerContainer = styled.div`
	width: 40%;
	height: 70%;
	margin: 0 auto;
	margin-top: 5%;
	background: white; 
	z-index: 99;
`

const Image = styled.img`
	height: 100%;
	width: 100%;	
`

const ButtonContainer = styled.div`
	transform: translateX(45%);
	margin-top: 10px;
`

const InnerButton = styled.div`
`

export const Notice = ({closeNotice}) => {
	return(
		<OuterContainer>
			<InnerContainer>
				<Image src={NoticeImage} />
				<ButtonContainer>
					<Button onClick={() => closeNotice()} text="Close" />
				</ButtonContainer>
			</InnerContainer>
		</OuterContainer>
	)
}
