import { useState, useEffect } from "react";

import { motion, AnimatePresence } from "framer-motion";
import styled from "styled-components";

import { colors, fontSize } from "../constants/styles.js";

const TitleStyled = styled.div`
	font-size: ${fontSize.sectionHeading};
	color: ${props => props.color === '' ? colors.grey9 : props.color};
	text-shadow: 5px 5px 3px #00000066;
	text-align: center;
`;

const SubTitleStyled = styled.div`
	font-size: ${fontSize.subTitle};
	color: ${colors.grey5};
	text-align: center;
	margin: 0 auto;
	padding: 10px 20px;
`;

const HeroTitleStyled = styled.div`
	font-size: ${fontSize.heroTitle};
	display: flex;
	flex-direction: column;
	color: white;
	margin-top: 8%;
	align-items: ${props => props.alignment}; 

	@media (max-width: 768px) {
		margin-top: 15%;
	}
`;

export const Title = ({ text, color = '' }) => <TitleStyled color={color}>{text}</TitleStyled>;

export const SubTitle = ({ text }) => <SubTitleStyled>{text}</SubTitleStyled>;

const FullPageTitleContainer = styled.div`	
	display: flex;
	height: 70vh;
	algin-items: center;
	justify-content: center;
`
const FullPageTitleStyled = styled.div`
	font-size: 105px;
	color: ${colors.black2};
	display: flex;
	align-items: center;
`

export const FullPageTitle = ({text}) => (<FullPageTitleContainer><FullPageTitleStyled>{text}</FullPageTitleStyled></FullPageTitleContainer>)

const titleStyle = {
	fontWeight: "bold", 
	textTransform: "uppercase",
	maxWidth: '50vw',
}

// Hero title below
export const HeroTitle = ({ preText, boldText, postText, alignment = '', addShadow = false }) => (
	<HeroTitleStyled alignment={alignment}>
		<div style={addShadow ? {color: colors.grey3} : {}}>{preText}</div>
		<div style={addShadow ? {textShadow: '5px 5px 3px #00000099', ...titleStyle}: titleStyle}>
			{window.screen.width < 768 ? <HeroControlMin text={boldText} />: <HeroControl text={boldText} />}
		</div>
		<div style={addShadow ? {color: colors.grey3} : {}}>{postText}</div>	
	</HeroTitleStyled>
);

const variants = {
	hidden: {
		opacity: 0,
	},
	visible: {
		opacity: 1,
		transition: {
			delay: 0.3,
			duration: 0.4,
			staggerChildren: 0.15,
			type: "tween",
		},
	},
	exit: {
		transition: { staggerChildren: 0.1 },
	},
};

const item = {
	hidden: {
		opacity: 0,
		y: 10,
	},
	visible: {
		opacity: 1,
		y: 0,
	},
	exit: {
		opacity: 0,
		y: 10,
	},
};

const HeroControlMin =({text}) =>{
	const textSplit = text.split('_')
	return(
		<>
			{textSplit.map((text, index) => <HeroControl text={text} delay={index} />)}
		</>
	)
}
const HeroText = ({ text, key, delay}) => {
	const textElements = text.split("").map((char, index) => {
		if (char === '_')
			return (
				<motion.div variants={item} key={index}>
					<div style={{ opacity: 0 }}>_</div>
				</motion.div>
			);
		return (
			<motion.div variants={item} key={index}>
				{char}
			</motion.div>
		);
	});

	return (
		<>
			<motion.div
				style={{ display: "flex" }}
				variants={variants}
				initial="hidden"
				animate="visible"
				exit="exit"
			>
				{textElements}
			</motion.div>
		</>
	);
};

const HeroControl = ({ text, delay }) => {
	const heroTextList = [text, text];
	const [textCounter, setTextCounter] = useState(0);

	useEffect(() => {
		// cycle through the text few sec
		const timeout = setTimeout(
			() =>
				setTextCounter(
					textCounter === heroTextList.length - 1 ? 0 : textCounter + 1
				),
			5000
		);

		return () => clearTimeout(timeout)
	}, [textCounter]);

	return (
		<>
			<AnimatePresence exitBeforeEnter>
				<HeroText
					text={heroTextList[textCounter]}
					key={textCounter === 0 ? 0 : heroTextList[textCounter - 1].length}
					delay={delay}
				/>
			</AnimatePresence>
		</>
	);
};


