import axios from 'axios'

const apiUrl = '/page'

const validateData = (data, dataName) => {
	if(data === '' || data === undefined || data === null) {
		return(
			{
				isValid: false, 
				info: `Enter a valid ${dataName}`
			}
		)
	}
	return({
		isValid: true,
		info: ''
	})
}

export const getAllPage = async () => {
	try{
		const result = await axios({	
			method: 'post', 
			url: `${apiUrl}/getAllPage`,
			data: {}
		})

		return result.data
	} catch(e) {
		console.log(e)
	}
}

export const addPage = async pageName => {
	let validate = validateData(pageName, 'Page Name')
	if(!validate.isValid) return validate.info; 

	try{
		const result = await axios({
			method: 'post', 
			url: `${apiUrl}/addPage`,
			data: {
				pageName: pageName
			}
		})

		return result.data
	} catch(e){
		console.log(e)
	}
}

export const removePage = async pageId => {
	try{
		const result = await axios({
			method: 'post', 
			url: `${apiUrl}/removePage`,
			data: {
				pageId: pageId
			}
		})

		return result.data
	} catch(e){
		console.log(e)
	}
}

export const addSection = async (pageId, sectionName, marker = '', text = []) => {
	let validate = validateData(sectionName, 'Section Name')
	if(!validate.isValid) return validate.info; 
	
	try{
		const result = await axios({
			method: 'post',
			url: `${apiUrl}/addSection`,
			data: {
				pageId: pageId, 
				sectionName: sectionName,
				marker: marker,
				text: text
			}
		})

		return result.data
	} catch(e){
		console.log(e)
	}
} 

export const editSection = async (pageId, sectionId, sectionName, marker, text) => {
	try{
		const result = await axios({
			method: 'post',
			url: `${apiUrl}/editSection`,
			data: {
				pageId: pageId, 
				sectionId: sectionId,
				sectionName: sectionName,
				marker: marker,
				text: text
			}
		})

		return result.data
	} catch(e){
		console.log(e)
	}
}

export const removeSection = async (pageId, sectionId) => {
	try{
		const result = await axios({
			method: 'post',
			url: `${apiUrl}/removeSection`,
			data: {
				pageId: pageId, 
				sectionId: sectionId,
			}
		})

		return result.data
	} catch(e){
		console.log(e)
	}
}

