const getFontSizes = () => {
	if(window.screen.width > 1300) {
		return {
			heroTitle: '80px',
			sectionHeading: '58px',
			innerHeading: '47px',
			footerBig: '35px',
			subTitle: '25px',
			textBig: '22px',
			text: '18px'
		}
	} else if(window.screen.width > 900){
		return {
			heroTitle: '48px',
			sectionHeading: '36px',
			innerHeading: '30px',
			footerBig: '30px',
			subTitle: '25px',
			textBig: '22px',
			text: '18px'
		}
	}	else {
		return {
			heroTitle: '32px',
			sectionHeading: '28px',
			innerHeading: '20px',
			footerBig: '20px',
			subTitle: '18px',
			textBig: '18px',
			text: '16px'
		}
	}
}
export const fontSize = getFontSizes()
