import { useState } from "react"

import styled from "styled-components"

import { Title } from "./Title"
import { Button } from "./Button"

import { fontSize } from "../constants/styles"

const ContactFormStyled = styled.div`
	display: flex;
	flex-direction: column;
	gap: 30px;
	width: 40%;

	@media (max-width: 768px) {
		width: 75%;
	}
`

const StyledFormComponent = styled.div``

const FormComponentContainer = styled.div`
	display: flex;
	gap: 50px;
`

const FormInput = styled.input`
	border-radius: 27px;
	border: none;
	font-size: ${fontSize.text};
	padding: 13px 24px;
	margin-top: 10px;
	width: 100%;

	&:focus {
		border: none;
		outline: none;
	}
`

const TextArea = styled.textarea`
	border-radius: 27px;
	border: none;
	font-size: ${fontSize.text};
	padding: 10px 20px;
	width: 100%;
	margin-top: 10px;
	min-height: 200px;

	&:focus {
		border: none;
		outline: none;
	}
`

const Text = styled.div`
	color: white;
	font-size: ${fontSize.subTitle};
`

const TitleContainer = styled.div`
	display: flex;
`

const InputContainer = styled.div`
	display: flex;
	justify-content: space-between;
	width: 100%;
	gap: 60px;
`

export const ContactForm = () => {
	const [name, setName] = useState("")
	const [email, setEmail] = useState("")
	const [message, setMessage] = useState("")

	const sendClicked = () => {
		const userData = {
			name : name, email: email, message: message
		}
		fetch("/util/sendMail", {
			method: "post",
			headers: {
				"Content-Type": "application/json",
			},
			body: JSON.stringify({ userData: userData }),
		}).then((data) => {
			setName('')
			setEmail('')
			setMessage('')
			alert("Form Submitted")
		})
	}

	return (
		<ContactFormStyled>
			<TitleContainer>
				<Title text="Have any Queries" color="white" />
			</TitleContainer>
			<FormComponentContainer>
				<InputContainer>
					<FormComponent
						text="Name"
						value={name}
						onChange={(e) => setName(e.target.value)}
					/>
					<FormComponent
						text="Email"
						value={email}
						onChange={(e) => setEmail(e.target.value)}
					/>
				</InputContainer>
			</FormComponentContainer>
			<TextBox
				text="Message"
				value={message}
				onChange={(e) => setMessage(e.target.value)}
			/>
			<InputContainer>
				<Button text="SEND" font="Rockwell" onClick={() => sendClicked()}/>
			</InputContainer>
		</ContactFormStyled>
	)
}

const FormComponent = ({ text, value, onChange }) => (
	<StyledFormComponent>
		<Text>{text}</Text>
		<FormInput value={value} onChange={onChange}/>
	</StyledFormComponent>
)

const TextBox = ({ text, value, onChange }) => (
	<StyledFormComponent>
		<Text>{text}</Text>
		<TextArea value={value} onChange={onChange}/>
	</StyledFormComponent>
)
