import styled from "styled-components"

import { Container } from "./Container"
import { Title, SubTitle } from "./Title"
import { CardContainer } from "./Card"

import Rect1 from "../assets/Rect 1.png"
import Rect2 from "../assets/Rect 2.png"
import Rect3 from "../assets/Rect 3.png"

import "react-responsive-carousel/lib/styles/carousel.min.css" // requires a loader
import { Carousel } from "react-responsive-carousel"

const cards = [
	{
		image: Rect1,
		title: "High School Dual-Degree Program",
		text: "The quick, brown fox jumps over a lazy dog. DJs flock by when MTV ax quiz prog. Junk MTV quiz graced by fox whelps. Bawds jog, flick quartz, vex nymphs. Waltz, bad nymph, for quick jigs vex! Fox nymphs grab quick-jived waltz. Brick quiz whangs jumpy veldt fox. Bright vixens jump; dozy fowl quack. Quick wafting zephyrs vex bold Jim. Quick zephyrs blow, vexing daft Jim. Sex-charged fop blew my junk TV quiz. How quickly daft jumping zebras vex",
		link: "DualDegree",
	},
	{
		image: Rect2,
		title: "The American School of Nepal",
		text: "The quick, brown fox jumps over a lazy dog. DJs flock by when MTV ax quiz prog. Junk MTV quiz graced by fox whelps. Bawds jog, flick quartz, vex nymphs. Waltz, bad nymph, for quick jigs vex! Fox nymphs grab quick-jived waltz. Brick quiz whangs jumpy veldt fox. Bright vixens jump; dozy fowl quack. Quick wafting zephyrs vex bold Jim. Quick zephyrs blow, vexing daft Jim. Sex-charged fop blew my junk TV quiz. How quickly daft jumping zebras vex",
		link: "ComingSoon",
	},
	{
		image: Rect3,
		title: "American Undergraduate Studies",
		text: "The quick, brown fox jumps over a lazy dog. DJs flock by when MTV ax quiz prog. Junk MTV quiz graced by fox whelps. Bawds jog, flick quartz, vex nymphs. Waltz, bad nymph, for quick jigs vex! Fox nymphs grab quick-jived waltz. Brick quiz whangs jumpy veldt fox. Bright vixens jump; dozy fowl quack. Quick wafting zephyrs vex bold Jim. Quick zephyrs blow, vexing daft Jim. Sex-charged fop blew my junk TV quiz. How quickly daft jumping zebras vex",
		link: "ComingSoon",
	},
]
export const Programs = ({ data }) => {
	return (
		<Container maxWidth={window.screen.width < 768 ? "98vw" : "90vw"}>
			<Title text="Our Programs" />
			<Container maxWidth={1100}>
				<SubTitle
					text="We at Green Circle Foundation provide different programs that are associated
					with the American Education through our partner educational insitutions in the United States of America"
				/>
			</Container>
			{window.screen.width < 1100 ? (
				<CardCarousel data={data} />
			) : (
				<CardContainers data={data} />
			)}
		</Container>
	)
}

const CardContainerStyled = styled.div`
	display: flex;
	justify-content: space-between;
	gap: 20px;
	margin-top: 45px;
	@media (max-width: 1100px) {
		position: relative;
		display: block;
	}
`

const CardContainers = ({ data }) => (
	<CardContainerStyled>
		<CardContainer
			image={cards[0].image}
			title={cards[0].title}
			text={data.sections[0].text ? data.sections[0].text : ''}
			link={cards[0].link}
		/>
		<CardContainer
			image={cards[1].image}
			title={cards[1].title}
			text={data.sections[1].text}
			link={cards[1].link}
		/>
		<CardContainer
			image={cards[2].image}
			title={cards[2].title}
			text={data.sections[2].text}
			link={cards[2].link}
		/>
	</CardContainerStyled>
)

const CardCarousel = ({ data }) => {
	const cardsComponent = cards.map((card, index) => (
		<CardContainer
			image={cards[index].image}
			title={cards[index].title}
			text={data.sections[index].text}
			link={cards[index].link}
		/>
	))

	const arrowStyles: CSSProperties = {
        position: 'absolute',
        zIndex: 2,
        top: 'calc(20% - 15px)',
        width: 30,
        height: 30,
        cursor: 'pointer',
        border: 'none',
        fontSize: '20px',
        background: 'none',
        padding: '20px',
        color: '#888',
        fontWeight: 'bold',
    };

	return (
		<CardContainerStyled>
			<Carousel
				renderArrowPrev={(onClickHandler, hasPrev, label) =>
					hasPrev && (
						<button
							type="button"
							onClick={onClickHandler}
							title={label}
							style={{ ...arrowStyles, left: 15 }}
						>
							&lt;
						</button>
					)
				}
				renderArrowNext={(onClickHandler, hasNext, label) =>
					hasNext && (
						<button
							type="button"
							onClick={onClickHandler}
							title={label}
							style={{ ...arrowStyles, right: 15 }}
						>
							&gt;
						</button>
					)
				}
			>
				{cardsComponent}
			</Carousel>
		</CardContainerStyled>
	)
}
