import { useState, useEffect, useRef } from "react"
import styled from "styled-components"

import { TextWrapContainer as Container } from "./Container"
import { Title } from "./Title"

import { fontSize, colors } from "../constants/styles"

const Main = styled.div`
`

const Shape = styled.div`
	height: ${(props) => {
		return props.height + "px"
	}};

	width: 50%;
	margin-right: 50px;
	float: left;
	clip-path: polygon(100% 0, 70% 50%, 100% 100%, 0 100%, 0 0);
	shape-outside: polygon(100% 0, 70% 50%, 100% 100%, 0 100%, 0 0);
	overflow: hidden;	
	display: flex;
	flex-direction: column;
	justify-content: space-between;

	@media (max-width: 1100px){
		display: none;
	}
`

const Shape2 = styled.div`
	height: ${(props) => {
		return props.height + "px"
	}};

	width: 50%;
	float: right;
	margin-left: 50px;
	clip-path: polygon(100% 0, 100% 100%, 0 100%, 30% 50%, 0 0);
	shape-outside: polygon(100% 0, 100% 100%, 0 100%, 30% 50%, 0 0);
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: space-between;

	@media (max-width: 1100px){
		display: none;
	}

`

const Image = styled.div`
	height: 49%;
	background: url("${(props) => props.background}") center no-repeat;
	background-size: 100% 100%;
`

const SingleWrapLeft = styled.div`
	height: ${(props) => {
		return props.height + "px"
	}};

	width: 50%;
	margin-right: 50px;
	float: left;
	clip-path: polygon(0 0, 100% 0, 70% 100%, 0% 100%);
	shape-outside: polygon(0 0, 100% 0, 70% 100%, 0% 100%);

	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	background: url('${props=> props.background}') no-repeat;
	background-size: cover;

	@media (max-width: 1100px){
		display: none;
	}

`
const SingleWrapLeft2 = styled.div`
	height: ${(props) => {
		return props.height + "px"
	}};

	width: 50%;
	margin-left: 50px;
	float: right;
	clip-path: polygon(0 0, 100% 0, 100% 100%, 30% 100%);
	shape-outside: polygon(0 0, 100% 0, 100% 100%, 30% 100%);

	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	background: url('${props=> props.background}') no-repeat;
	background-size: cover;

	@media (max-width: 1100px){
		display: none;
	}

`

const Hide = styled.div`
	background: #009245;
	opacity: .45;
	height: 100%;
	width: 100%;
`

const TextContainer = styled.div`
	position: relative;
	right: 0;
`

const TextContainer2 = styled.div`
	position: relative;
	left: 0;
	width: 100%;
`

const TitleContainer = styled.div`
	padding-right: 40px;
	border-right: 5px solid ${colors.green5};
	margin-bottom: 20px;

	display: flex;
	justify-content: flex-end;
`

const TitleContainer2 = styled.div`
	padding-left: 40px;
	border-left: 5px solid ${colors.green5};
	margin-bottom: 20px;

	display: flex;
`

const Text = styled.div`
	font-size: ${fontSize.text};
	color: ${colors.grey5};
	white-space: pre-line;
`

const RectImage = styled.div`
	height: 200px;
	width: 100%;
	background: url('${props => props.background}') no-repeat;
	background-size: cover;
	margin-bottom: 15px;
`
const RectImageLeft = styled(RectImage)`
	background-position: 100% 100%;
`

export const TextWrap = ({ text, title = "Fill", image1, image2 }) => {
	const [height, setHeight] = useState(0)
	const ref = useRef(null)

	useEffect(() => {
		setHeight(ref.current.clientHeight * 1.8)
	}, [text])

	return (
		<Container maxWidth={window.screen.width < 768 ? '95vw' : '90vw'}>
			<Main>
				<Shape height={height}>
					<Image background={image1} />
					<Image background={image2} />
				</Shape>
				<TextContainer>
					<TitleContainer>
						<Title text={title} />
					</TitleContainer>
					{window.screen.width < 1100 && <RectImageLeft background={image1} />}
					<Text ref={ref}>{text}</Text>
				</TextContainer>
			</Main>
		</Container>
	)
}

export const TextWrap2 = ({ text, title = "Fill", image1, image2 }) => {
	const [height, setHeight] = useState(0)
	const ref2 = useRef(null)

	useEffect(() => {
		setHeight(ref2.current.clientHeight * 1.8)
	}, [text])

	return (
		<Container maxWidth={window.screen.width < 1100 ? '95vw' : '90vw'}>
			<div style={{ marginTop: "30px" }} />
			<Main>
				<Shape2 height={height}>
					<Image background={image1} />
					<Image background={image2} />
				</Shape2>
				
				<TextContainer2>
					<TitleContainer2>
						<Title text={title} />
					</TitleContainer2>
					{window.screen.width < 1100 && <RectImage background={image1} />}
					<Text ref={ref2}>{text}</Text>
				</TextContainer2>
			</Main>
		</Container>
	)
}

export const SingleWrap = ({ text, title = "", image }) => {
	const [height, setHeight] = useState(0)
	const ref = useRef(null)

	useEffect(() => {
		setHeight(ref.current.clientHeight * 1.65)
	}, [text])

	return (
		<Container maxWidth={window.screen.width < 768 ? '95vw' : '90vw'}>
			<Main>
				<SingleWrapLeft height={height} background={image}>
					<Hide></Hide>
				</SingleWrapLeft>
				<TextContainer>
					<TitleContainer>
						<Title text={title} />
					</TitleContainer>
					{window.screen.width < 1100 && <RectImage background={image} />}
					<Text ref={ref}>{text}</Text>
				</TextContainer>
			</Main>
		</Container>
	)
}

export const SingleWrap2 = ({ text, title = "" , image}) => {
	const [height, setHeight] = useState(0)
	const ref = useRef(null)

	useEffect(() => {
		setHeight(ref.current.clientHeight * 1.8)
	}, [text])

	return (
		<Container maxWidth={window.screen.width < 768 ? '95vw' : '90vw'}>
			<Main>
				<SingleWrapLeft2 height={height} background={image}>
					<Hide></Hide>
				</SingleWrapLeft2>
				<TextContainer2>
					<TitleContainer2>
						<Title text={title} />
					</TitleContainer2>
					{window.screen.width < 1100 && <RectImage background={image} />}
					<Text ref={ref}>{text}</Text>
				</TextContainer2>
			</Main>
		</Container>
	)
}
