import { useState, useEffect} from "react"

import { Hero } from "../components/Hero"
import { Programs } from "../components/Programs"
import { Partners } from "../components/Partners"
import { Footer } from "../components/Footer"
import { SectionSeperator } from "../components/SectionSeperator"
import { Notice } from "../components/Notice"

import getPageData from "../api/content.js"

const Home = () => {
	const [pageData, setPageData] = useState({})
	const [dataLoaded, setDataLoaded] = useState(false)
	const [testComp, setTestComp] = useState(() => <h1>Working</h1>)
	const [showNotice, setShowNotice] = useState(true)

	useEffect(() => {
		const setData = async () => {
			const result = await getPageData("Index")
			setPageData({ ...result })
			setDataLoaded(true)
		}

		setData()
	}, [])

	useEffect(() => {
		if(dataLoaded) {
			const newTest = () => (
				<>
					<Programs data={pageData} />
					<Partners />
				</>
			)
			setTestComp(newTest)
		}
	},[dataLoaded])

	const closeNotice = () => {
		setShowNotice(false)
	}
	return (
		<SectionSeperator>
			<Hero />
			{showNotice ? <Notice closeNotice={closeNotice} /> : '' }
			<div id="test" />
			{dataLoaded ? <>{testComp}</> : <h1>Loading...</h1>}
			<Footer />
		</SectionSeperator>
	)
}

export default Home
