import useToken from './hooks/useToken.js'

import Dashboard from './components/Dashboard.jsx'
import InputComponent from './components/InputComponent.jsx'

import Button from '@material-ui/core/Button'

const roles = [
  {
    username: 'admin',
    password: 'admin'
  }, 
  {
    username: 'superadmin',
    password: 'superadmin'
  }
]

function App() {
  const {token, setToken, clearToken} = useToken()

  const authenticateUser = (username, password) => {
    roles.forEach(role => {
      if(role.username === username && role.password === password) {
        if(role.username === 'superadmin') {
          setToken({value: "superadmin"})
          return
        }
        setToken({value: "admin"})
        return 
      }
    })
  }

  if(!token) {
    return  <>
      <InputComponent authenticateUser={authenticateUser}/>
      <a href='index.html'><Button variant='contained' style={{position: 'absolute', top: '2%', right: '2%'}}>To Site</Button></a>
    </>
  }

  const logout = () => {
    clearToken()
  }

  return (
    <>
      <Dashboard isSuperAdmin={token.value === 'superadmin' ? true : false} />
      <div style={{position: 'absolute', top: '2%', right: '2%'}}>
        <a href='index.html' style={{marginRight: '15px', textDecoration: 'none', color: 'black'}}><Button variant='contained'>To Site</Button></a>
        <Button variant='contained' onClick={logout}>Logout</Button>
      </div>
    </>
  );
}

export default App;
