import styled from "styled-components"

import { Icon } from "./Icon"
import { IconWithText } from "./IconWithText"
import { Image } from "./Image"
import { Link } from "react-router-dom"

import { fontSize } from "../constants/styles"

import Logo from "../assets/Logo.png"
import Globe from "../assets/Icon feather-globe.png"
import Email from "../assets/Icon material-email.png"
import Phone from "../assets/Icon material-local-phone.png"
import Location from "../assets/Icon material-location-on.png"
import Facebook from "../assets/Icon awesome-facebook.png"
import Instagram from "../assets/Icon awesome-instagram.png"

const FooterStyled = styled.div`
	padding-top: 30px;
	color: white;
	background: transparent linear-gradient(180deg, #00924580 0%, #39b54a80 100%)
		0% 0% no-repeat padding-box;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 15px;
`

const InnerStyles = styled.div`
	display: flex;
	flex-direction: column;
	gap: 8px;
`

const InnerStylesBig = styled.div`
	display: flex;
	flex-direction: column;
	gap: 20px;
`

const Container = styled.div`
	display: flex;
	gap: 20px;
`

const ContainerBig = styled.div`
	display: flex;
	gap: 40px;
	margin-top: 50px;

	@media (max-width: 768px) {
		gap: 20px;
		margin-top: 20px;
	}
`

const CopyrightStyled = styled.p`
	margin-top: -10px;
`

export const Footer = () => (
	<FooterStyled>
		<Image
			src={Logo}
			height={window.screen.width < 768 ? 100 : 145}
			width={window.screen.width < 768 ? 100 : 145}
			alt="Logo"
		/>
		<IconTexts />
		<Socials />
		<CopyrightStyled>
			&copy; Green Circle{" "}
			<Link
				to="/dashboard"
				style={{ color: "white", textDecoration: "none", cursor: "text" }}
			>
				Foundation
			</Link>{" "}
			2021
		</CopyrightStyled>
	</FooterStyled>
)

export const IconTexts = () => (
	<InnerStyles>
		<IconWithText
			icon={
				<Icon
					icon={<img src={Location} width={18} height={27} alt="Address" />}
					color="white"
				/>
			}
			text="Budanilkantha-08, Kathmandu"
		/>
		<IconWithText
			icon={
				<Icon
					icon={<img src={Globe} width={18} height={18} alt="Website" />}
					color="white"
				/>
			}
			text="www.gcfeducation.com"
		/>
		<IconWithText
			icon={
				<Icon
					icon={<img src={Email} width={18} height={14} alt="E-mail" />}
					color="white"
				/>
			}
			text="info@gcfeducation.com"
		/>
		<IconWithText
			icon={
				<Icon
					icon={<img src={Phone} width={18} height={18} alt="Phone-no" />}
					color="white"
				/>
			}
			text="9851236949,9851121892"
		/>
	</InnerStyles>
)

export const IconTextsBig = () => (
	<InnerStylesBig>
		<IconWithText
			icon={
				<Icon
					icon={
						<img
							src={Location}
							width={window.screen.width < 768 ? 20 : 30}
							height={window.screen.width < 768 ? 30 : 48}
							alt="Address"
						/>
					}
					color="white"
				/>
			}
			text="Budanilkantha-08, Kathmandu"
			passedFontSize={fontSize.footerBig}
			gap={fontSize.innerHeading}
		/>
		<IconWithText
			icon={
				<Icon
					icon={
						<img
							src={Globe}
							width={window.screen.width < 768 ? 20 : 30}
							height={window.screen.width < 768 ? 20 : 30}
							alt="Website"
						/>
					}
					color="white"
				/>
			}
			text="www.gcfeducation.com"
			passedFontSize={fontSize.footerBig}
			gap={fontSize.innerHeading}
		/>
		<IconWithText
			icon={
				<Icon
					icon={
						<img
							src={Email}
							width={window.screen.width < 768 ? 20 : 32}
							height={window.screen.width < 768 ? 16 : 25}
							alt="E-mail"
						/>
					}
					color="white"
				/>
			}
			text="info@gcfeducation.com"
			passedFontSize={fontSize.footerBig}
			gap={fontSize.innerHeading}
		/>
		<IconWithText
			icon={
				<Icon
					icon={
						<img
							src={Phone}
							width={window.screen.width < 768 ? 20 : 30}
							height={window.screen.width < 768 ? 20 : 30}
							alt="Phone-no"
						/>
					}
					color="white"
				/>
			}
			text="9851236949,9851121892"
			passedFontSize={fontSize.footerBig}
			gap={fontSize.innerHeading}
		/>
	</InnerStylesBig>
)

export const Socials = () => (
	<Container>
		<a href="https://www.facebook.com/gcfedunp/" target="_blank" rel="noreferrer">
			<Icon
				icon={
					<img src={Facebook} width={35} height={35} alt="Facebook page link" />
				}
			/>
		</a>
		<a href="https://www.instagram.com/gcfnepal/" target="_blank" rel="noreferrer">
			<Icon
				icon={
					<img
						src={Instagram}
						width={35}
						height={35}
						alt="Instagram page link"
					/>
				}
			/>
		</a>
	</Container>
)

export const SocialsBig = () => (
	<ContainerBig>
		<a href="https://www.facebook.com/gcfedunp/" target="_blank" rel="noreferrer">
			<Icon
				icon={
					<img
						src={Facebook}
						width={fontSize.innerHeading}
						height={fontSize.innerHeading}
						alt="Facebook page link"
					/>
				}
			/>
		</a>
		<a href="https://www.instagram.com/gcfnepal/" target="_blank" rel="noreferrer">
			<Icon
				icon={
					<img
						src={Instagram}
						width={fontSize.innerHeading}
						height={fontSize.innerHeading}
						alt="Instagram page link"
					/>
				}
			/>
		</a>
	</ContainerBig>
)
