import { useState, useEffect } from "react"
import getPageData from "../api/content.js"
import {AboutHero} from '../components/Hero'
import { TextWrap, TextWrap2 } from "../components/TextWrap"
import { Partners } from "../components/Partners"
import { Footer } from "../components/Footer"
import {SectionSeperator} from '../components/SectionSeperator'

import AboutS1I1 from "../assets/About_s1_i1.png"
import AboutS1I2 from "../assets/About_s1_i2.png"
import AboutS2I1 from "../assets/About_s2_i1.png"
import AboutS2I2 from "../assets/About_s2_i2.png"

const About = () => {
	const [workingText1, setWorkingText1] = useState('This is working text')
	const [title1, setTitle1] = useState('Title 1')
	const [title2, setTitle2] = useState('Title 2')
	const [workingText2, setWorkingText2] = useState('This is working text')

	useEffect(() => {
		const setData = async () => {
			const result = await getPageData("About")

			setWorkingText1(result.sections[0].text[0])
			setWorkingText2(result.sections[1].text[0])
			setTitle1(result.sections[0].sectionName)
			setTitle2(result.sections[1].sectionName)
		}

		setData()
	}, [])

	return(
		<SectionSeperator>
			<AboutHero />
			<TextWrap text={workingText1} title={title1} image1={AboutS1I1} image2={AboutS1I2}/>
			<TextWrap2 text={workingText2} title={title2} image1={AboutS2I1} image2={AboutS2I2}/>
			<Partners />
			<Footer />
		</SectionSeperator>
	)
}

export default About