import { useState, useEffect} from "react"
import { TextWrap, SingleWrap, SingleWrap2 } from "../components/TextWrap"
import { SectionSeperator } from "../components/SectionSeperator"
import { Footer } from "../components/Footer"
import { DualDegreeHero } from "../components/Hero"

import AboutS1I1 from "../assets/About_s1_i1.png"
import AboutS1I2 from "../assets/About_s1_i2.png"
import GraduationImage from '../assets/graduation.jpg'
import GraduationImage2 from '../assets/graduation2.jpg'
import Benefits from '../assets/benefits.jpg'


import getPageData from "../api/content.js"

export default () => {
	const [pageData, setPageData] = useState({})
	const [dataLoaded, setDataLoaded] = useState(false)

	useEffect(() => {
		const setData = async () => {
			const result = await getPageData("DualDegree")
			setPageData({ ...result })
			setDataLoaded(true)
		}

		setData()
	}, [])

	return (
		<SectionSeperator>
			<DualDegreeHero />
			<TextWrap
				text={dataLoaded ? pageData.sections[0].text[0] : ""}
				title={dataLoaded ? pageData.sections[0].sectionName : ""}
				image1={AboutS1I1} image2={AboutS1I2}
			/>
			<SingleWrap2
				text={dataLoaded ? pageData.sections[1].text[0] : ""}
				title={dataLoaded ? pageData.sections[1].sectionName : ""}
				image={Benefits}
			/>
			<SingleWrap
				text={dataLoaded ? pageData.sections[2].text[0] : ""}
				title={dataLoaded ? pageData.sections[2].sectionName : ""}
				image={GraduationImage}
			/>
			<SingleWrap2
				text={dataLoaded ? pageData.sections[3].text[0] : ""}
				title={dataLoaded ? pageData.sections[3].sectionName : ""}
				image={GraduationImage2}
			/>
			<Footer />
		</SectionSeperator>
	)
}
