import styled from "styled-components"

import { Container } from "./Container"
import { Image } from "./Image"
import { Title, SubTitle } from "./Title"

import Img1 from "../assets/Logo - AMS.png"
import Img2 from "../assets/NCSchool.png"
import Img3 from "../assets/Logo - CSULB.png"

export const Partners = () => (
	<Container maxWidth={window.screen.width < 768 ? '98vw' : '60vw'}>
		<Title text="Our Partners" />
		<SubTitle
			text="Green Circle Foundation for American Education has partnership with a number of educational 
				institutions in the United States of America"
		/>
		<ImageContainer />
	</Container>
)

const ImageContainerStyled = styled.div`
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 20px;

	@media (max-width: 1100px) { 
		flex-wrap: wrap;
		justify-content: center;

		> * {
			&:nth-child(1) {
				flex: 100%;
				display: flex;
				justify-content: center;
			}
			&:nth-child(2) {
			}
			&:nth-child(3) {
				margin-left: 20px;
			}
		}
	}
`

const ImageContainer = () => {
	const imgDimBig = {
		i1:{
			h:227,w:271
		},
		i2: {
			h:227, w: 276 
		},
		i3: {
			h:140, w: 296
		}
	}

	const imgDimSmall = {
		i1:{
			h:99,w:120
		},
		i2: {
			h:87, w: 87 
		},
		i3: {
			h:62, w: 130
		}
	}

	const imgHeight = window.screen.width < 768 ? imgDimSmall : imgDimBig

	return	(
		<ImageContainerStyled>
			<Image src={Img2} height={imgHeight.i2.h} className="i2" width={imgHeight.i2.w} />
			<Image src={Img1} height={imgHeight.i1.h} className="i1" width={imgHeight.i1.w} />
			<Image src={Img3} height={imgHeight.i3.h} className="i3" width={imgHeight.i3.w} />
		</ImageContainerStyled>
	)
}
