import { Link } from "react-router-dom"
import styled from "styled-components"

import { Container } from "./Container"
import { Image } from "./Image"
import { HeroTitle, FullPageTitle } from "./Title"
import { Menu } from "./Menu"
import { Button } from "./Button"
import { IconTextsBig , SocialsBig } from "./Footer"
import { ContactForm } from "./ContactForm"

import Logo from "../assets/Logo.png"
import Background from "../assets/Hero Section Image.png"
import AboutBackground from "../assets/Background.png"
import ContactBackground from "../assets/ContactBackground.png"

const HeroContainer = styled.div`
	height: calc(100vw * .5);
	background: url("${(props) => props.background}") center no-repeat;
	background-size: 100%;
	padding-top: 20px;
	background-position: 100% 0;

	@media (max-width: 1100px) {
		height: calc(100vw * .50);
	}

	@media (max-width: 768px) {
		height: 50vh;
		background-size: cover;
		background-position: 100% 100%;
	}
`

const ContactHeroContainer = styled(HeroContainer) `
	height: 100%;
	min-height: 100vh;
	background-size: cover;
`

const HeadingContainer = styled.div`
	display: flex;
	justify-content: space-between;
`

const ImageAdjustment = styled.div`
	@media (min-width: 1100px){
		position: relative;
		left: -10px;
	}
`

const FooterContactStyled = styled.div`
		color: white;
		display: flex;
		flex-direction: column;
		align-items: center;
`

const ContactInnerContainer = styled.div`
	display: flex;
	align-items: center;
	margin-top: 60px;

	@media(max-width: 768px) {
		flex-direction: column;
		align-items: flex-start;
	}
`

const ContactsStyled = styled.div`
	display: flex;
	justify-content: center;
	width: 60%;
	margin-top: 80px;

	@media (max-width: 768px) {
		width: 75%;
		margin-top: 50px;
	}
`

const ButtonContainer = styled.div`
	display: flex; 
	justify-content: center;
	margin-top: 8px;

	@media (max-width: 768px) {
		justify-content: flex-start;
	}
`

const TopBar = ({color = null}) => {
	const imgHeight = window.screen.width < 768 ? 50 : 100

	return (
		<HeadingContainer>
			<ImageAdjustment>
				<Link to="/">
					<Image src={Logo} height={imgHeight} width={imgHeight} />
				</Link>
			</ImageAdjustment>
			<Menu color={color}/>
		</HeadingContainer>
	)
}

export const Hero = () => (
	<HeroContainer background={Background}>
		<Container maxWidth={window.screen.width < 768 ? '95vw' : '90vw'}>
			<TopBar />
			<HeroTitle
				preText="Introducing"
				boldText="American_Education"
				postText="In Nepal"
			/>
			<div style={{ marginTop: window.screen.width < 768 ? '10%' : '6%' }} />
			<a href="/pages/applicationForm.html"><Button text="Join Us" /></a>
		</Container>
	</HeroContainer>
)

export const AboutHero = () => (
	<HeroContainer background={AboutBackground}>
		<Container maxWidth={window.screen.width < 768 ? '95vw' : '90vw'}>
			<TopBar />
			<HeroTitle
				preText="Introducing"
				boldText="American_Education"
				postText="In Nepal"
				addShadow={true}
				alignment={window.screen.width < 768 ? "left" :"center"}
			/>
		</Container>
	</HeroContainer>
)


export const ContactHero = () => {
	return (
		<ContactHeroContainer background={ContactBackground}>
			<Container maxWidth={window.screen.width < 768 ? '95vw' : '90vw'}>
				<TopBar />
				<ContactInnerContainer>
				 	<ContactForm />
					<ContactsStyled><FooterContact /></ContactsStyled>
				</ContactInnerContainer>
			</Container>
		</ContactHeroContainer>
	)
}

export const FooterContact = () => (
	<FooterContactStyled>
		<IconTextsBig />
		<SocialsBig />
	</FooterContactStyled>
)

export const DualDegreeHero = () => (
	<HeroContainer background={AboutBackground}>
		<div id="#top" />
		<Container maxWidth={window.screen.width < 768 ? '95vw' : '90vw'}>
			<TopBar />
			<HeroTitle
				preText="High School"
				boldText="Dual-Degree"
				postText="Program"
				addShadow={true}
				alignment={window.screen.width < 768 ? "left" :"center"}
			/>
			<div style={{ marginTop: window.screen.width < 768 ? '10%' : '6%' }} />
			<ButtonContainer><a href="/pages/applicationForm.html"><Button text="Join Now" /></a></ButtonContainer>
		</Container>
	</HeroContainer>
)

export const ComingSoonHero = () => (
	<HeroContainer>
		<Container>
			<TopBar color="#000"/>
			<FullPageTitle text="Coming Soon..." />
		</Container>
	</HeroContainer>
)

