import {useState, useEffect, useMemo} from 'react'
import {v4 as uuid} from 'uuid'

import {makeStyles} from '@material-ui/core/styles'
import Dialog from '@material-ui/core/Dialog' 
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';

import {
	getAllPage,
	addPage,
	removePage,
	addSection, 
	editSection, 
	removeSection
} from '../api/page.js'

const useStyles = makeStyles({
	container: {
		padding: '.6rem',
		display: 'flex',
		flexDirection: 'column',
		width: '30rem'
	},
	marginVertical: {
		marginTop: '0.6rem',
		marginBottom: '0.6rem',
	},
	marginHorizontal: {
		marginLeft: '0.6rem',
		marginRight: '0.6rem',
	},
	marginRight: {
		marginRight: '0.6rem',
	}
})

const Dashboard = ({isSuperAdmin}) => {
	const classes = useStyles() 

	const [allPageData, setAllPageData] = useState([])
	const [showAllPage, setShowAllPage]= useState(true)
	const [currentPage, setCurrentPage] = useState({})

	const isDisabled = useMemo(() => {return isSuperAdmin ? false  : true}, [])
	// get all the page data
	useEffect(() => {
		const fetchAllPage = async () => {
			const result = await getAllPage()

			setAllPageData(result)
		}

		fetchAllPage()
	},[])

	// add the newly created page data to the pageData state
	const addToAllPageData = (newData) => {
		setAllPageData(allPageData => [...allPageData, newData])
	}

	// open the individual page
	const openPage = (data) => {
			setCurrentPage({...data})
			setShowAllPage(false)
	}

	const deletePage = async (page, index) => {
		const result = await removePage(page._id)

		if(result.error !== undefined){
			alert('An error has occured')
			return
		}

		if(result.nModified !== 0) {
			setAllPageData(allPagedata => [...allPageData.filter((data, number) => index !== number)])
		}
	}

	// Change back from showing one page to all pages
	const toAllPage = () => {
		setShowAllPage(true)
	}

	// Little snippets for all the page data -- might have to change this later on in case openPage gets closed
	const pageSnippets = useMemo(() => allPageData.map((page, index) => (
			<div key={page._id} className={classes.marginVertical}>
				<Typography variant='h5'>{page.pageName}</Typography>
				<Button variant='contained' onClick={() => openPage(page)} className={classes.marginRight} size='small'>Page data</Button>
				<Button variant='contained' onClick={() => deletePage(page, index)}  className={classes.marginRight} size='small' disabled={isDisabled} >Remove page</Button>
			</div>
		)
	), [allPageData])

	return(
		<Container>
			{/*Either show all pages or show an individual page*/}
			{
				showAllPage ? 
				<>
				<h1>Dashboard</h1>
				<AddPage addToAllPageData={addToAllPageData} isDisabled={isDisabled}/>
				{pageSnippets} 
				</>
				: 
				<div>
					<Button onClick={toAllPage} variant='contained' size="small"
					>Go Back</Button>
					<ShowPage page={currentPage} isDisabled={isDisabled}/>
				</div>
			}
		</Container>
	)
}

const AddPage = ({addToAllPageData, isDisabled}) => {
	const classes = useStyles()

	const [showModal, setShowModal] = useState(false)
	const [pageName, setPageName] = useState('')

	const addPageClick = () => {
		setShowModal(true)
	}

	const submitAddPage = async () => {
		if(pageName === '') {
			alert('Cannot add empty page name')
			return
		}
		const result = await addPage(pageName)

		addToAllPageData(result)
		setShowModal(false)
		setPageName('')
	}

	return(
		<>
			<Button onClick={addPageClick} variant='contained' disabled={isDisabled}>AddPage</Button>
			<Dialog open={showModal} onClose={() => setShowModal(false)}>
				<DialogTitle className={classes.marginVertical}>Insert Page name</DialogTitle>
				<DialogContent>
					<div className={classes.marginVertical}>
						<TextField value={pageName} onChange={e => setPageName(e.target.value)} placeholder='Page name'/>
						<Button onClick={submitAddPage} variant='contained'>Submit</Button> 
					</div>
				</DialogContent>
			</Dialog>
		</>
	)
}

const ShowPage = ({page, isDisabled}) => {
	const classes = useStyles() 

	const [sectionsData, setSectionsData] = useState(page.sections)
	const [showAddSectionModal, setShowAddSectionModal] = useState(false)
	const [newSectionName, setNewSectionName] = useState('')

	// Add section to the page
	const addNewSection = async () => {
		if(newSectionName === ''){
			alert('Add a section name')
			return
		}
		const result = await addSection(page._id, newSectionName)

		if(result.error !== undefined) {
			alert('An error has occured. Pleas try again')
			setNewSectionName('')
			setShowAddSectionModal(false)
			return 
		}

		setSectionsData([...result.sections])
		setShowAddSectionModal(false)
		setNewSectionName('')
	}

	// Save the edits
	const saveEdits = async (sectionId, sectionName, marker, text) => {
		const result = await editSection(page._id, sectionId, sectionName, marker, text)
		if(result.error === undefined) return true; 
	}

	// Remove an individual section
	const deleteSection = async sectionId => {
		const result = await removeSection(page._id, sectionId)

		if(result.error !== undefined) {
			alert('An error has occured. Pleas try again')
			return
		}

		setSectionsData([...result.sections])
	}

	// Show existing sections
	const sections = useMemo(() => sectionsData.map((data, index) => (
			<div key={data.sectionName} className={classes.marginVertical}>
				<Section data={data} saveEdits={saveEdits} deleteSection={deleteSection} isDisabled={isDisabled}/>
			</div>
		)
	), [sectionsData]) 

	return(
		<>
				<Typography>Page Name: {page.pageName}</Typography>
				{sections}
				<Button onClick={() => setShowAddSectionModal(true)} variant='contained' size='small' disabled={isDisabled}>Add New Section </Button>
				{
					setShowAddSectionModal ? 
					<Dialog open={showAddSectionModal} onClose={() => setShowAddSectionModal(false)}>
						<TextField value={newSectionName} onChange={e => setNewSectionName(e.target.value)} placeholder='Section Name' />
						<Button onClick={addNewSection}>Add Section</Button>
					</Dialog>
					: ''
				}
		</>
	)
}

const Section = ({data, saveEdits, deleteSection, isDisabled}) => {
	const classes = useStyles()

	const [openEditSection, setOpenEditSection] = useState(false)
	const [showInnerDialog, setShowInnerDialog] = useState(false)
	const [sectionName, setSectionName] = useState(data.sectionName)
	const [marker, setMarker] = useState(data.marker)
	const [text, setText] = useState(data.text)
	const [textKeys, setTextKeys] = useState([])
	const [dataChanged, setDataChanged] =  useState(false)

	useEffect(() => {
		let tempKeys = []
		text.forEach(() => {
			tempKeys = [...tempKeys, uuid()]
		})
		setTextKeys([...tempKeys])
	}, [text.length])

	// Edit the section name and text
	const editSection = () => {
		setOpenEditSection(true)
	}

	// Save the sections edited data
	const saveSection = async () => {
		const result = await saveEdits(data._id, sectionName, marker, text)
		if(result === true) {
			setOpenEditSection(false)
		}

		setDataChanged(false)
	}

	const removeSection = () => {
		deleteSection(data._id)
	}

	// Change of data
	const sectionNameChange = e => {
		setSectionName(e.target.value)
		setDataChanged(true)
	}

	const textChange = (e, index) => {
		let newText = text
		newText[index] = e.target.value
		setText([...newText])
		setDataChanged(true)
	}

	const removeText = index => {
		setText(text => [...text.filter((data, number) => index !== number)])
		setTextKeys(key => [...key.filter((data, number) => index !== number)])
		setDataChanged(true)
	}

	const addText = () => {
		setText(text => [...text, ''])
		setDataChanged(true)
	}

	const markerChange = e => {
		setMarker(e.target.value)
		setDataChanged(true)
	}

	// Dialog logic
	const dialogClose = () => {
		if(dataChanged === true){
			setShowInnerDialog(true)
			return
		}
		setOpenEditSection(false)
	}

	const innerDialogClose = () => {
		setShowInnerDialog(false)
	}

	const innerDialogButtonClick = isExit => {
		if(isExit) {
			setShowInnerDialog(false)
			setOpenEditSection(false)
			return
		}
		setShowInnerDialog(false)
	}

	const textComponents = text.map((data, index) => (
			<div key={textKeys[index]}>
					<TextField 
						value={text[index]} 
						onChange={(e) => textChange(e, index)} 
						label='Text' 
						className={classes.marginVertical}
						multiline={true} 
						rows={5} 
						rowMax={Infinity} 
						cols={12}
						fullWidth={true}
					/>
					<Button onClick={() => removeText(index)} disabled={isDisabled}>Remove</Button>
			</div>
		)
	)
	return(
		<>
			<Typography variant='h6'>Section Name: {data.sectionName}</Typography>
			<Button onClick={editSection} className={classes.marginRight} variant='contained'>Edit Section</Button>
			<Button onClick={removeSection} className={classes.marginRight} variant='contained' disabled={isDisabled}>Remove Section</Button>
			{
				openEditSection ? 
				<Dialog open={openEditSection} onClose={dialogClose}>
					<div className={classes.container}>
					<TextField value={sectionName} onChange={sectionNameChange} label='Section Name' className={classes.marginVertical} />
					<TextField value={marker} onChange={markerChange} label='Marker' className={classes.marginVertical} />
					{textComponents}
					<Button onClick={addText}>Add Text</Button>
					<Button onClick={saveSection}>Save</Button>
					{/*Dialog to inform user they're leaving before saving*/}
					{
						showInnerDialog ? 
							<Dialog open={showInnerDialog} onClose={innerDialogClose}>
								<Typography variant='body1'>You haven't saved your data yet. Are you sure you want to exit.</Typography>
								<Button variant='contained' onClick={() => innerDialogButtonClick(true)} size='small'>Yes</Button>
								<Button variant='contained' onClick={() => innerDialogButtonClick(false)} size='small'>No</Button>
							</Dialog>
						: ''
					}
					</div>
				</Dialog>
				: ''
			}
		</>
	)
}

export default Dashboard
// Remove pages