import styled from "styled-components"

import { colors, fontSize } from "../constants/styles.js"

import {TransparentButton as Button} from './Button'

const TextStyled = styled.div`
	display: flex;
	justify-content: center;
	text-align: center;
	max-width: 300px;
	position: relative;
	text-shadow: 5px 5px 3px #00000099;


	transition: transform .25s; 
`

const ButtonContainer = styled.div`
		opacity: 0;
		transition: opacity .25s;
		
		position: absolute; 
		top: 70%;
		transform: translateY(-50%);
`

const CardStyled = styled.div`
	height: 530px;
	background-size: 100% 100%;
	background-image: url("${(props) => props.image}");
	font-size: ${fontSize.innerHeading};
	font-weight: bold;
	color: white;

	position: relative;

	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;

	&:hover ${TextStyled} {
		transform: translateY(-100px);
	}

	&:hover ${ButtonContainer} {
		opacity: 1;
	}

	@media (max-width: 768px) {
		height: 230px;
		width: 200px;
		padding: 40px;

		${TextStyled} {
				transform: translateY(-50px);	
		}

		${ButtonContainer} {
			opacity: 1;
		}
	}
`

export const Card = ({ image, title, buttonText = 'Find Out More', link = '' }) => (
	<CardStyled image={image}>
		<TextStyled>{title}</TextStyled>
		<ButtonContainer><Button text={buttonText} link={link}/></ButtonContainer>
	</CardStyled>
)

const CardContainerStyled = styled.div`
	display: flex;
	flex-direction: column;
	width: 470px;

	@media (max-width: 1100px) {
		margin: 0 auto;
	}
	@media (max-width: 768px) {
		width: 80vw;
		margin: 0 auto;
		align-items: center;
		text-align: center;
	}
`

const SmallText = styled.div`
	font-size: ${fontSize.text};
	color: ${colors.grey5};
	font-style: italic; 
`

export const CardContainer = ({ image, title, text, link }) => (
	<CardContainerStyled>
		<Card image={image} title={title} link={link}/>
		<SmallText>{text}</SmallText>
	</CardContainerStyled>
)
