import { useState } from "react"
import styled from "styled-components"
import { HashLink as Link } from "react-router-hash-link"
import { motion, AnimatePresence } from "framer-motion"

import { fontSize } from "../constants/styles"

import Search from "../assets/Icon material-search.png"
import Hamburger from "../assets/Icon material-menu.png"
import HamburgerBlack from "../assets/Menu Black.png"
import SearchBlack from "../assets/Search Icon Black.png"

const MenuStyled = styled.div`
	flex: 1;
	display: flex;
	gap: 140px;
	justify-content: flex-end;
	margin-top: 20px;

	@media (max-width: 1100px) {
		gap: 80px;
	}
`

const LinkStyled = styled(Link)`
	color: ${(props) => props.color};
	font-family: "Helvetica";
	font-size: ${fontSize.subTitle};
	text-decoration: none;
	font-weight: bold;

	@media (max-width: 768px) {
		display: none;
	}
`

const LinkHamburger = styled(LinkStyled)`
		display: block;
		font-size: 40px;
		color: #fff;

`

const IconStyled = styled.div`
	color: ${(props) => props.color};
	font-family: "Helvetica";
	font-size: ${fontSize.subTitle};
	font-weight: bold;
	display: flex;
	gap: 40px;

	> * {
		&:first-child {
			@media (max-width: 768px) {
				display: none;
			}
		}
	}
`

const HMenuContainer = styled.div`
	position: fixed;

	width: 100vw;
	height: 100vh;
	z-index: 9;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: #478556;

	display: flex;
	justify-content: center;
`

const CloseMenu = styled.button`
	border: none;
	background: none;
	color: #000;

	font-size: 20px;
	padding: 20px;

	position: fixed;
	right: 10%;
	top: 2%;
`
const HInnerMenu = styled.div`
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 20px;
	margin-top: 10%;

	@media(max-width: 1100px) {margin-top: 40%;}
`

export const Menu = ({ color = null }) => {
	const colorToUse = color === null ? "white" : "black"
	const [toggleMenu, setToggleMenu] = useState(false)

	return (
		<>
			<MenuStyled>
				<LinkStyled to="/about" color={colorToUse}>
					About Us
				</LinkStyled>
				<LinkStyled to="/home#test" color={colorToUse}>
					Programs
				</LinkStyled>
				<LinkStyled to="/contact" color={colorToUse}>
					Contact Us
				</LinkStyled>
				<IconStyled color={colorToUse}>
					<img
						src={color === null ? Search : SearchBlack}
						className="search"
						width={25}
						height={25}
						style={{ cursor: "pointer" }}
						alt="Search"
					/>
					<img
						src={color === null ? Hamburger : HamburgerBlack}
						width={25}
						height={25}
						style={{ cursor: "pointer" }}
						onClick={() => setToggleMenu(true)}
						alt="HamburgerMenu"
					/>
				</IconStyled>
			</MenuStyled>
			<AnimatePresence>
			{toggleMenu && <HamburgerMenu closeMenu={() => setToggleMenu(false)} />}
			</AnimatePresence>
		</>
	)
}

const HamburgerMenu = ({ closeMenu }) => {
	return (
		<HMenuContainer
			as={motion.div}
			animate={{ y: 0 }}
			initial={{ y: -1000 }}
			exit={{ y: -1000 }}
			transition={{ duration: 0.3 }}
		>
			<CloseMenu onClick={closeMenu}><span style={{color: 'white', fontSize: '200%'}}>X</span></CloseMenu>
			<HInnerMenu >
				<LinkHamburger to="/" color="black" onClick={closeMenu}>
					Home
				</LinkHamburger>
				<LinkHamburger to="/about" color="black" onClick={closeMenu}>
					About Us
				</LinkHamburger>
				<LinkHamburger to="/home#test" color="black" onClick={closeMenu}>
					Programs
				</LinkHamburger>
				<LinkHamburger to="/contact" color="black" onClick={closeMenu}>
					Contact Us
				</LinkHamburger>
			</HInnerMenu>
		</HMenuContainer>
	)
}
