import styled from "styled-components"

import { fontSize } from "../constants/styles.js"

const IconWithTextStyled = styled.div`
	display: flex;
	gap: ${(props) => props.gap};
	font-size: ${(props) => props.fontSize};
	align-items: center;
`

export const IconWithText = ({ icon, text, passedFontSize, gap }) => (
	<IconWithTextStyled
		fontSize={passedFontSize === undefined ? fontSize.textBig : passedFontSize}
		gap={gap === undefined ? "10px" : gap}
	>
		{icon} {text}
	</IconWithTextStyled>
)
