import styled from "styled-components";
import {HashLink as Link} from 'react-router-hash-link'

import { colors, fontSize } from "../constants/styles";

const ButtonStyled = styled.button`
	border: none;
	background: #e6e6e6 0% 0% no-repeat padding-box;
	color: ${colors.green4};
	font-size: ${fontSize.subTitle};
	font-weight: bold;
	border-radius: 20px;
	padding: 8px 20px;
	cursor: pointer;
	font-family: ${props => props.font};
`;

const TransButtonStyled = styled.button`
	background-color: #ffffff00;
	border: 3px solid ${colors.white};
	outline: none;
	color: ${colors.white};
	font-size: ${fontSize.subTitle};
	border-radius: 60px;
	padding: 10px 22px;
	cursor: pointer;
	font-family: 'Rockwell';

	@media (max-width: 768px) {
		padding: 5px 10px;
	}
`;

export const Button = ({ text, font = 'Arial', onClick = () => {} }) => <ButtonStyled font={font} onClick={onClick}>{text}</ButtonStyled>;

export const TransparentButton = ({ text, link = ''  }) => (
	<Link to={`${link}#top`}><TransButtonStyled>{text}</TransButtonStyled></Link>
);
